}
.fee .total {
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  /* identical to box height */

  color: #3e3e3e;
}
.fee .text {
  font-weight: 300;
  font-size: 16px;
  line-height: 18px;

  color: #3e3e3e;
}
